.cd-tabs {
  padding-left: 0;
  .cd-clearfix;
  margin-bottom: 24px;
  line-height: 1;
  > li {
    float: left;
    list-style: none;
    + li {
      margin-left: 40px;
    }
    > a {
      color: @cd-dark-minor;
      .cd-transition;
      display: block;
      padding-bottom: 16px;
      border-bottom: 2px solid transparent;
      font-weight: 500;
      font-size: 14px;
      &:hover {
        .cd-transition;
        color: @cd-dark-major;
      }
    }
    &.active {
      > a {
        color: @cd-dark-major;
        border-color: @brand-primary;
      }
    }
  }
  .number {
    color: @brand-warning;
    margin-left: 4px;
    vertical-align: top;
  }
}

.cd-tabs-content {
  
}

.cd-tabs-panel {
  display: none;
  &.active {
    display: block;
  }
}


