
.site-clearfix:after,
.site-clearfix:before {
  content: " ";
  display: table;
}
.site-clearfix:after {
  clear: both;
}
.site-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  padding: 0 0 0 200px;
  height: 56px;
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.01), 0 4px 4px 0 rgba(0, 0, 0, 0.04);
  -moz-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.01), 0 4px 4px 0 rgba(0, 0, 0, 0.04);
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.01), 0 4px 4px 0 rgba(0, 0, 0, 0.04);
  z-index: 99;
}
.site-header__logo {
  position: absolute;
  top: 0;
  left: 0;
  height: 56px;
  line-height: 32px;
  padding: 12px 40px;
  width: 200px;
  color: #43bc60;
}
.site-header__nav {
  margin: 0 auto;
}
.site-header__nav > ul {
  list-style: none;
  float: right;
  margin: 0 auto;
  padding: 0 40px;
}
.site-header__nav > ul > li {
  line-height: 20px;
  padding: 18px 0;
  margin: 0 32px;
  font-size: 14px;
  float: left;
  color: rgba(0, 0, 0, 0.56);
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.site-header__nav > ul > li:hover {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.88);
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.site-header__nav > ul > li.active {
  color: rgba(0, 0, 0, 0.88);
}
.site-sidebar {
  position: fixed;
  top: 56px;
  left: 0;
  bottom: 0;
  width: 200px;
  overflow: auto;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.01), 0 4px 4px 0 rgba(0, 0, 0, 0.04);
}
.site-sidebar__nav {
  list-style: none;
  padding-left: 8px;
}
.site-sidebar__nav-item {
  padding: 16px;
  display: block;
}
.site-sidebar__nav-item .site-sidebar__nav-item__title {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.32);
}
.site-sidebar__nav-item .site-sidebar__nav-item__subtitle {
  font-size: 14px;
  padding-left: 8px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.56);
}
.site-sidebar__nav-item .site-sidebar__nav-item__subtitle.active,
.site-sidebar__nav-item .site-sidebar__nav-item__subtitle:hover {
  color: #43bc60;
}
.site-sidebar__nav-item .site-sidebar__nav-item__subtitle.active .nav-en,
.site-sidebar__nav-item .site-sidebar__nav-item__subtitle:hover .nav-en {
  color: #43bc60;
}
.site-sidebar__nav-item .site-sidebar__nav-item__subtitle:hover {
  cursor: pointer;
}
.site-sidebar__nav-item .site-sidebar__nav-item__subtitle .nav-en {
  font-size: 12px;
  margin-left: 4px;
  color: rgba(0, 0, 0, 0.32);
}
.site-homepage-banner {
  padding: 150px 0;
  width: 1200px;
  margin: 56px auto 0;
}
.site-homepage-banner:after,
.site-homepage-banner:before {
  content: " ";
  display: table;
}
.site-homepage-banner:after {
  clear: both;
}
.site-homepage-banner__text {
  float: left;
  width: 280px;
  padding-top: 40px;
}
.site-homepage-banner__text h1 {
  color: #43bc60;
  font-size: 32px;
  line-height: 48px;
  margin-bottom: 16px;
}
.site-homepage-banner__text h2 {
  color: rgba(0, 0, 0, 0.88);
  line-height: 16px;
  font-size: 16px;
  margin-bottom: 24px;
}
.site-homepage-banner__pic {
  position: relative;
  width: 920px;
  height: 480px;
  float: left;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.site-homepage-banner__boss {
  position: absolute;
  z-index: 2;
  bottom: 60px;
  left: 180px;
  opacity: 0;
}
.site-homepage-banner__boss.active {
  opacity: 1;
  bottom: 70px;
  left: 190px;
  transition: all 0.3s 0.6s ease;
}
.site-homepage-banner__boss.active.prohibit {
  transition: none;
}
.site-homepage-banner__designer {
  position: absolute;
  z-index: 3;
  bottom: 150px;
  left: 425px;
  opacity: 0;
}
.site-homepage-banner__designer.active {
  opacity: 1;
  bottom: 160px;
  transition: all 0.3s ease;
}
.site-homepage-banner__designer.active.prohibit {
  transition: none;
}
.site-homepage-banner__engineer {
  position: absolute;
  z-index: 3;
  bottom: 88px;
  left: 600px;
  opacity: 0;
}
.site-homepage-banner__engineer.active {
  opacity: 1;
  bottom: 98px;
  transition: all 0.3s 0.3s ease;
}
.site-homepage-banner__engineer.active.prohibit {
  transition: none;
}
.site-homepage-banner__computer {
  position: absolute;
  z-index: 1;
  bottom: 56px;
  left: 115px;
  width: 697px;
  height: 435px;
  opacity: 0;
}
.site-homepage-banner__computer.active {
  bottom: 46px;
  opacity: 1;
  transition: all 0.3s ease;
}
.site-homepage-banner__computer.active.prohibit {
  transition: none;
}
.site-homepage-banner__vase {
  position: absolute;
  z-index: 1;
  left: 125px;
  bottom: 190px;
  opacity: 0;
}
.site-homepage-banner__vase.active {
  bottom: 180px;
  opacity: 1;
  transition: all 0.3s ease;
}
.site-homepage-banner__vase.active.prohibit {
  transition: none;
}
.site-homepage-banner__component {
  position: absolute;
  z-index: 2;
  left: 380px;
  bottom: 190px;
  opacity: 0;
}
.site-homepage-banner__component.active {
  bottom: 180px;
  opacity: 1;
  transition: all 0.3s ease;
}
.site-homepage-banner__component.active.prohibit {
  transition: none;
}
.site-homepage-banner__desktop {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -10px;
  z-index: 0;
  opacity: 0;
}
.site-homepage-banner__desktop.active {
  bottom: 0;
  opacity: 1;
  transition: all 0.3s ease;
}
.site-homepage-banner__desktop.active.prohibit {
  transition: none;
}
.site-homepage-feature {
  width: 1200px;
  margin: 0 auto 180px;
  display: flex;
}
.site-homepage-feature:after,
.site-homepage-feature:before {
  content: " ";
  display: table;
}
.site-homepage-feature:after {
  clear: both;
}
.site-homepage-feature__item {
  flex: 1;
  flex-direction: column;
  text-align: center;
  width: 280px;
  justify-content: space-between;
}
.site-homepage-feature__thumb {
  margin-bottom: 32px;
}
.site-homepage-feature__title {
  color: rgba(58, 58, 58, 0.88);
  font-size: 16px;
  margin-bottom: 16px;
}
.site-homepage-feature__content {
  color: rgba(0, 0, 0, 0.56);
  font-size: 14px;
  margin-bottom: 24px;
  line-height: 22px;
}
.site-homepage-footer {
  font-size: 14px;
  padding: 32px 0;
  background: #43bc60;
  -webkit-box-shadow: 0 -4px 4px 0 rgba(0, 0, 0, 0.01), 0 -4px 4px 0 rgba(0, 0, 0, 0.04);
  -moz-box-shadow: 0 -4px 4px 0 rgba(0, 0, 0, 0.01), 0 -4px 4px 0 rgba(0, 0, 0, 0.04);
  box-shadow: 0 -4px 4px 0 rgba(0, 0, 0, 0.01), 0 -4px 4px 0 rgba(0, 0, 0, 0.04);
}
.site-homepage-footer:after,
.site-homepage-footer:before {
  content: " ";
  display: table;
}
.site-homepage-footer:after {
  clear: both;
}
.site-homepage-footer__nav {
  padding-left: 0;
  float: left;
  margin: 0 auto;
}
.site-homepage-footer__nav > li {
  list-style: none;
  float: left;
  margin: 0 32px;
}
.site-homepage-footer__nav > li > a {
  color: rgba(255, 255, 255, 0.8);
}
.site-homepage-footer__nav > li > a:hover {
  color: #ffffff;
}
.site-homepage-footer__copyright {
  float: right;
  color: rgba(255, 255, 255, 0.8);
}
body {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Helvetica Neue, Helvetica, Arial, PingFang SC, Hiragino Sans GB, Microsoft YaHei, sans-serif;
  background-color: #fff;
  overflow-x: hidden;
  margin: 0;
  color: rgba(0, 0, 0, 0.56);
}
pre {
  margin-top: 0;
  margin-bottom: 24px;
  background-color: #fafafa;
}
pre code {
  border-radius: 4px;
}
.cd-table code {
  margin: 1px;
  background: #f7f7f7;
  padding: .2em .4em;
  border-radius: 3px;
  font-size: .9em;
  border: 1px solid #eee;
  display: inline-block;
}
.hljs {
  padding: 0 8px !important;
  border-radius: 4px;
  color: rgba(0, 0, 0, 0.56) !important;
  background-color: #fafafa !important;
}
.site-main {
  position: fixed;
  top: 56px;
  left: 200px;
  right: 0;
  bottom: 0;
  overflow-y: auto;
}
.site-content {
  position: relative;
  width: 90%;
  min-height: 800px;
  margin: 0 auto;
  border-radius: 4px;
  background-color: #fff;
  padding: 48px 24px;
}
.site-content-title {
  font-size: 32px;
  font-weight: 500;
  color: #43bc60;
  margin-bottom: 32px;
}
.site-content-des {
  font-size: 14px;
  margin-bottom: 32px;
}
.site-content-subtitle {
  font-size: 22px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.88);
  margin-bottom: 24px;
}
.site-panel {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  font-size: 12px;
  margin-bottom: 24px;
}
.site-panel .site-panel__heading {
  padding: 16px 16px 24px 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
}
.site-panel .site-panel__title {
  position: absolute;
  top: -8px;
  left: 8px;
  line-height: 16px;
  padding: 0 8px;
  background-color: #fff;
}
.site-panel .site-panel__body {
  position: relative;
  padding: 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
}
.site-panel .site-panel__footer {
  padding: 0 16px;
  background-color: #f8f8f8;
}
.site-pager {
  padding: 48px 0;
}
.site-pager .site-page:hover {
  cursor: pointer;
  color: #43bc60;
}
.site-pager .site-page.site-page-right {
  float: right;
}



